.about_info {
    margin: 0 auto;
    padding-top: 15em;
    max-width: fit-content;
}

p {
    font-size: 20px;
}

.writer {
    overflow: hidden;
    white-space: nowrap;
    animation-name: typing;
    animation-duration: 1.5s steps(350, end);
    animation-name: blink-caret;
    animation-duration: .75s step-end infinite;
}

.writer2 {
    overflow: hidden;
    white-space: nowrap;
    animation:
        typing 1.5s steps(350, end),
        blink-caret .75s step-end infinite;
}

.writer3 {
    overflow: hidden;
    white-space: nowrap;
    animation:
        typing 1.5s steps(350, end),
        blink-caret .75s step-end infinite;
}

.writer4 {
    overflow: hidden;
    white-space: nowrap;
    animation:
        typing 3.5s steps(50, end),
        blink-caret .75s step-end infinite;
}


@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
}






