.about_info {
    margin: 0 auto;
    padding-top: 15em;
    max-width: fit-content;
}

p {
    font-size: 20px;
}

.writer {
    overflow: hidden;
    white-space: nowrap;
    animation:
        typing 4.5s steps(100, end),
        blink-caret .75s step-end infinite;
}

.writer2 {
    overflow: hidden;
    white-space: nowrap;
    animation:
        typing 4.5s steps(100, end),
        blink-caret .75s step-end infinite;
}


@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
}


@media (max-width: 768px) {
  /* Styles for screens up to 768px wide (e.g., mobile devices) */
  .about_info {
    padding: 20px;
    align-items: center;
    margin-top: 30px;
  }

  .writer,
  .writer2,
  .writer3,
  .writer4 {
    margin-top: 30px;
    align-items: center;
    animation: none;
    font-size: 16px;
    line-height: 1.4;
    white-space: normal;
  }
}






