header {
    height: 100vh;
    overflow: hidden;
}

.typewriter {
    overflow: hidden;
    border-right: .1em solid orange;
    white-space: nowrap; 
    margin: 0 auto;
    max-width: fit-content;
    padding-bottom: 10px;
    animation: 
      typing 1s steps(350, end),
      blink-caret .5s step-end infinite;
}
/* 
.typewriter.typing {
    animation:
      typing 9.5s steps(10, end),
      blink-caret .5s step-end infinite;
} */


.hashString {
    padding-top: 15px;
}



input {
    margin: 0 auto;
    max-width: fit-content;
    margin-top: 10px;
    font-size: 20px;
    padding: 5px 10px;
    background-color: black;
    color: chartreuse;
    border: 2px solid;
    border-color: chartreuse;
    border-radius: 5px;
    outline: none;
    text-align: center;
}

button {
    margin: 0 auto;
    max-width: fit-content;
    margin-top: 10px;
    font-size: 20px;
    padding: 5px 10px;
    background-color: black;
    color: chartreuse;
    border: 2px solid;
    border-color: chartreuse;
    border-radius: 5px;
    animation: none;
    transition: animation 0.5s ease;
}
  
  button:hover {
    animation: bounce 1s infinite;
}
  
@keyframes bounce {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0);
    }
}

.socials {
    margin-top: 30px;
    font-size: 15px;
    animation: none;
    transition: animation 0.5s ease;
  }
  
.socials:hover {
    animation: bounce 1s infinite;
}
  
@keyframes bounce {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0);
    }
}

p {
    font-size: clamp(0.9rem, 2.5vw, 1.2rem);
}

.response {
    margin: 20px;
    margin-top: 20px;
}

#content {
    text-align: center;
    margin: 0 auto;
    max-width: fit-content;
    margin-top: 15rem;
}

@keyframes typing {
from { width: 0 }
to { width: 100% }
}

@keyframes blink-caret {
from, to { border-color: transparent }
50% { border-color: orange }
}

#about {
    display: none;
}


@media (max-width: 768px) {

    #content {
      padding: 20px;
      height: 100vh;
    }
  
    h1 {
      font-size: 20px;
    }
    
    button {
      font-size: 14px;
      padding: 8px 20px;
    }

    .hashString {
        font-size: .9rem;
        overflow-wrap: break-word;
    }
}
  


